.Admin-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    background-color: #fafafa;
    height: 100vh;
    width: 100vw;
}

.TimeClock-input {
    height: 3rem;
    width: 300px;
}

.Event-Title-input {
    height: 3rem;
    width: 300px;
}

.TSF-admin-button {
    height: 3rem;
    width: 14rem;
    background-color: #85C1E9;
    border: 1px solid black;
    border-radius: 3rem;
    font-size: 1rem;
    -webkit-transition-duration: 0.4s; /* Safari */
    -o-transition-duration: 0.4s;
       transition-duration: 0.4s;
}

.TSF-admin-button:hover {
    background-color: #239B56;
    color: white;
}


